package com.jonnyzzz.logo.script

class ExpressionParseException(val pos: Int, message: String) : Exception(message)

interface ExpressionTokenSource {
  /**
   * Represents position in the original source
   */
  val pos: Int

  val current : Char?

  fun next()

  fun hasMoreTokens(): Boolean

  fun parseError(message: String) : Nothing

  /**
   * Creates a copy of the TokenSource from current position
   */
  fun forkTokenSource() : ExpressionTokenSource

  fun onlyReadBefore(endPosInclusive: ExpressionTokenSource) : ExpressionTokenSource
}

fun ExpressionTokenSource(s: String) : ExpressionTokenSource {
  return ExpressionTokenSourceImpl(s)
}

class ExpressionTokenSourceImpl(
  private val originalText: String,
  private var offset: Int = 0,
  private val endOffset: Int = originalText.length - 1,
) : ExpressionTokenSource {
  override fun forkTokenSource(): ExpressionTokenSource {
    return ExpressionTokenSourceImpl(originalText, offset, endOffset)
  }

  override fun onlyReadBefore(endPosInclusive: ExpressionTokenSource): ExpressionTokenSource {
    endPosInclusive as ExpressionTokenSourceImpl
    require(this.originalText === endPosInclusive.originalText)
    return ExpressionTokenSourceImpl(originalText, offset, endPosInclusive.offset)
  }

  override val pos
    get() = offset

  override val current : Char? get() = when {
    hasMoreTokens() -> originalText[offset]
    else -> null
  }

  override fun next() {
    if (hasMoreTokens()) {
      offset++
    }
  }

  override fun hasMoreTokens() = offset <= endOffset

  override fun parseError(message: String) : Nothing {
    throw ExpressionParseException(pos, "Failed to parse token at $offset: $message")
  }

  override fun toString() = "ExpressionTokenSource($pos, '${originalText.take(offset)}_\$_${originalText.drop(offset)}')"
}

fun ExpressionTokenSource.noMoreTokens() = !hasMoreTokens()

fun ExpressionTokenSource.skipWhitespaces() {
  while(current?.isWhitespace() == true) next()
}

fun ExpressionTokenSource.readInt(): Int {
  skipWhitespaces()

  if (current?.isDigit() != true) {
    parseError("Number was expected, but found: $current")
  }

  var r = 0
  while(true) {
    val current = current
    r = r * 10 + when {
      current == null -> break
      current.isDigit() -> current.code - '0'.code
      current.isWhitespace() -> break //could be a next expression
      current.isLetter() -> parseError("Number was expected, but found: $current")
      else -> break //could be a separator, we handle that no the higher level
    }
    next()
  }

  return r
}

fun ExpressionTokenSource.readWord(): String {
  skipWhitespaces()

  if (current?.isLetter() != true) {
    parseError("Work was expected, but found: $current")
  }

  var r = ""
  while(true) {
    val current = current
    r += when {
      current == null -> break
      current.isLetterOrDigit() -> current
      else -> break //could be a separator, we handle that no the higher level
    }
    next()
  }

  return r
}

fun ExpressionTokenSource.currentMatches(
  prefix: String,
  charMatcher: (actual: Char, expected: Char) -> Boolean = { a, b -> a.equals(b, true) }
): Boolean {
  require(prefix.isNotEmpty()) { "Prefix must not be empty" }

  //TODO: inefficient, check char0 first
  val view = forkTokenSource()
  for (actual in prefix) {
    val expected = view.current ?: return false
    if (!charMatcher(actual, expected)) return false
    view.next()
  }

  return true
}


fun ExpressionTokenSource.readAll(): String {
  val b = StringBuilder()
  while (hasMoreTokens()) {
    b.append(current ?: error("Unexpected end of stream $this reading until $pos"))
    next()
  }
  return b.toString()
}
