package com.jonnyzzz.logo.web

import kotlinx.js.jso
import org.w3c.dom.HTMLElement
import kotlin.js.Promise

@JsModule("@codemirror/state")
private external val CodeMirrorStateModule: dynamic

@JsModule("@codemirror/view")
private external val CodeMirrorViewModule: dynamic

@JsModule("@codemirror/commands")
private external val CodeMirrorCommandsModule: dynamic

@JsModule("@codemirror/lint")
private external val CodeMirrorLintModule: dynamic


/** can actually be an array of extensions, every level deep **/
external interface CodeMirrorExtension

external interface CodeMirrorText {
  val length: Number
  val lines: Number
}

external interface CodeMirrorTransaction {
  val docChanged: Boolean

  val state: CodeMirrorState
  val newDoc: CodeMirrorText
}

external interface CodeMirrorTransactionSpec

external interface CodeMirrorState {
  val doc: CodeMirrorText

  fun sliceDoc(from: Number = definedExternally, to: Number = definedExternally): String

  fun update(spec: Array<CodeMirrorTransactionSpec>): CodeMirrorTransaction
}

external interface CodeMirrorSelection

external interface CodeMirrorSelectionRange

@Suppress("UnsafeCastFromDynamic")
fun codeMirrorSelectionCursor(pos: Number): CodeMirrorSelectionRange {
  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorStateModule.EditorSelection.cursor(pos)
}

fun codeMirrorSelectionCreate(vararg ranges: CodeMirrorSelectionRange): CodeMirrorSelection {
  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorStateModule.EditorSelection.create(ranges)
}

private val codeMirrorDefaultKeymap: dynamic
  get() = CodeMirrorCommandsModule.defaultKeymap

private val codeMirrorHistoryImpl : () -> CodeMirrorExtension
  @Suppress("UnsafeCastFromDynamic")
  get() = CodeMirrorCommandsModule.history


private fun codeMirrorKeymapOf(keymap: dynamic): CodeMirrorExtension {
  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorViewModule.keymap.of(keymap)
}

fun codeMirrorReadonlyState(
  doc: String,
): CodeMirrorState {
  return codeMirrorStateImpl(
    doc = doc,
    extensions = arrayOf(
      @Suppress("UnsafeCastFromDynamic")
      CodeMirrorViewModule.editable.of(false),
    )
  )
}

fun codeMirrorState(
  doc: String,
  extensions: Array<CodeMirrorExtension> = arrayOf(),
  selection: CodeMirrorSelection? = null,
): CodeMirrorState {
  val mergedExtensions = arrayOf<CodeMirrorExtension>(
    codeMirrorKeymapOf(codeMirrorDefaultKeymap),
    //https://codemirror.net/docs/ref/#commands.history
    codeMirrorHistoryImpl(),
    //https://codemirror.net/docs/ref/#view.highlightActiveLine
    @Suppress("UnsafeCastFromDynamic")
    CodeMirrorViewModule.highlightActiveLine(),
    *extensions
  )
  return codeMirrorStateImpl(doc, mergedExtensions, selection)
}

private external interface CodeMirrorStateConfig {
  val doc: String
  val extensions: Array<CodeMirrorExtension>?
  val selection: CodeMirrorSelection?
}

private fun codeMirrorStateImpl(
  doc: String,
  extensions: Array<CodeMirrorExtension> = arrayOf(),
  selection: CodeMirrorSelection? = null,
): CodeMirrorState {
  val jso = object : CodeMirrorStateConfig {
    override val doc = doc
    override val extensions = extensions
    override val selection = selection
  }
  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorStateModule.EditorState.create(jso)
}

external class CodeMirrorView {
  val state: CodeMirrorState
  fun setState(newState: CodeMirrorState)

  fun focus()
  fun destroy()
  fun dispatch(transaction: CodeMirrorTransaction)
  fun update(transactions: Array<CodeMirrorTransaction>)
}

fun CodeMirrorView(
  state: CodeMirrorState,
  parent: HTMLElement,
  dispatcher: (CodeMirrorTransaction) -> Unit,
): CodeMirrorView {
  @Suppress("UNUSED_VARIABLE")
  val params: dynamic = jso {
    this.state = state
    this.parent = parent
    this.dispatch = dispatcher
  }

  @Suppress("UNUSED_VARIABLE", "LocalVariableName")
  val EditorView = CodeMirrorViewModule.EditorView

  @Suppress("UnsafeCastFromDynamic")
  return js("new EditorView(params)")
}

external interface CodeMirrorDiagnostic

fun codeMirrorDiagnostic(
  from: Number,
  to: Number,
  severity: String,
  message: String,
  //todo: actions: Array<> = definedExternally
) : CodeMirrorDiagnostic {
  @Suppress("UnsafeCastFromDynamic")
  return jso<dynamic> {
    this.from = from
    this.to = to
    this.severity = severity
    this.message = message
  }
}

@Suppress("UnsafeCastFromDynamic")
fun codeMirrorLintGutter(): CodeMirrorExtension {
  return CodeMirrorLintModule.lintGutter({})
}

fun codeMirrorLinter(
  source: (CodeMirrorView) -> Promise<Array<CodeMirrorDiagnostic>>,
): CodeMirrorExtension {
  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorLintModule.linter(source)
}

fun codeMirrorSetDiagnostics(
  state: CodeMirrorState,
  diagnostics: Array<CodeMirrorDiagnostic>,
): CodeMirrorTransactionSpec {

  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorLintModule.setDiagnostics(state, diagnostics)
}

fun codeMirrorForceLinting(
  view: CodeMirrorView,
) {
  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorLintModule.forceLinting(view)
}
